import React, { Component } from "react";

class Footer extends Component {
  state = {
    year: "",
  };
  componentDidMount() {
    const date = new Date();
    this.setState({ year: date.getFullYear() });

  }

  render() {
    return (
      <footer
        style={{ maxWidth: "100%", textDecoration: "none", marginTop: "5px" }}
      >
        <div className="container mt-2">
          <div className="layout-justify">
            <p className="rights">
              <span className="icofont-bird-wings red"> Win24</span>
              <span>&nbsp;&nbsp;</span>
              <span className="copyright-year">2017-{this.state.year}</span>
              <span>&nbsp;</span>
            </p>
          </div>
        </div>
      </footer>
    );
  }
}

export default Footer;
