import React, { Component } from "react";
import { getJwt } from "../services/authServices";
import http from "../services/httpServices";
import { apiEndPoint } from "../endPoins";

import Loader2 from "../loader copy";

http.setJwtForClub(getJwt());
class ClubUser extends Component {
  state = {
    tableHead: [
      { id: "1", header: "SN" },
      { id: "2", header: "Name" },
      { id: "3", header: "UseName" },
      { id: 4, header: "Join Date" },
      { id: 7, header: " last bet" },
      { id: 5, header: "total_bet" },
      { id: 6, header: "total_commission" },
    ],
    tableData: [
      {
        id: 0,
        username: "",
        first_name: "",
        last_name: "",
        join_date: "",
        last_bet: "",
        full_name: "",
        total_bet: "",
        total_commission: "",
      },
    ],
    isLoading: true,
  };
  async componentDidMount() {
    try {
      this.setState({ isLoading: true });
      const dataOfUser = await http.get(apiEndPoint("api/club-user/"));
      const user = dataOfUser.data.results;
      user.map((item, index) => {
        item.serial = index + 1;
        return null;
      });

      this.setState({ tableData: user });
    } catch (err) {}
    this.setState({ isLoading: false });
  }
  render() {
    const { isLoading } = this.state;

    return (
      <>
        {isLoading && <Loader2 />}
        {!isLoading && (
          <div className="mt-4 container">
            <br />
            <div className="gamesSliderBox">
              <span className="sssiloyIconBox sprite isotopeIconBox pt-2">
                <span className=" icofont-chart-flow-1" />
                <p>User Of your</p>
              </span>
            </div>
            <table className="table table-dark table-striped ">
              <thead>
                <tr>
                  {this.state.tableHead.map(({ id, header }) => {
                    return (
                      <th scope="col" key={id}>
                        {header}
                      </th>
                    );
                  })}
                </tr>
              </thead>
              <tbody>
                {this.state.tableData.map(
                  ({
                    first_name,
                    serial,
                    username,
                    last_name,
                    join_date,
                    last_bet,
                    total_bet,
                    total_commission,
                  }) => {
                    return (
                      <tr>
                        <td>{serial}</td>
                        <td>
                          {first_name} {last_name}
                        </td>
                        <td>{username}</td>
                        <td>{join_date && join_date.split("T")[0]}</td>
                        <td>{last_bet && last_bet.split("T")[0]}</td>
                        <td>{total_bet}</td>
                        <td>{total_commission}</td>
                      </tr>
                    );
                  }
                )}
              </tbody>
            </table>
            {this.state.tableData.length === 0 && (
              <div className="container text-center">
                <h5>You Do not have any users !</h5>
              </div>
            )}
          </div>
        )}
      </>
    );
  }
}

export default ClubUser;
