import React, { Component } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { apiEndPoint } from "../endPoins";
import "./homePage.css";
import http from "../services/httpServices";
import Loader from "../loader";
import InPlayHelper from "../inPlay/inPlayHelper";
import Deposite from "../login/deposite";
import WithDeaw from "../login/withDraw";
import { getUserByJwt } from "../services/authServices";
import { getJwt } from "../services/userService";

http.setJwtForClub(getJwt());
http.setJwt(getJwt());

class HomePage extends Component {
  state = {
    allData: [
      { id: 5, name: "All Games" },
      { id: 1, name: "Football" },
      { id: 2, name: "Cricket" },
      { id: 3, name: "Tennis" },
      { id: 4, name: "Others" },
    ],
    nameOfMatchId: [],
    currentGameName: {
      gameName: "All Games",
      itemCount: "",
    },
    data: [],
    announcement: [],
    currentGameData: [],
    liveGameId: [],
    upcommingGameId: [],
    liveGameData: [],
    upcommingGameData: [],
    matchesQuestion: {},
    matchId: null,
    isLoading: true,
    isLive: true,
    user: "",
  };

  async componentDidMount() {
    try {
      const datapak = await http.get(
        apiEndPoint("api/match/?active_only=true")
      );

      if (datapak.data.results.length > 0) {
        const data = datapak.data.results.sort(this.compare);

        const currentGameName = { ...this.state.currentGameName };
        const matchesQuestion = {};
        const liveGameId = [];
        const upcommingGameId = [];
        const upcommingGameData = [];
        const liveGameData = [];
        var res = await http.get(apiEndPoint("api/bet_scope/?limit=100000"));
        res = res.data.results.sort(this.compare);
        for (var item of res) {
          if (item.match in matchesQuestion) {
            matchesQuestion[item.match].push(item);
          } else {
            matchesQuestion[item.match] = [];
            matchesQuestion[item.match].push(item);
          }
        }
        for (var item1 of data) {
          if (item1.is_live === true) {
            liveGameId.push(item1.id);
          } else {
            upcommingGameId.push(item1.id);
          }
        }
        for (var item2 of data) {
          if (liveGameId.includes(item2.id)) {
            liveGameData.push(item2);
          } else {
            upcommingGameData.push(item2);
          }
        }

        this.setState({
          matchesQuestion,
          data,
          currentGameData: liveGameData,
          currentGameName,
          upcommingGameId,
          liveGameId,
          liveGameData,
          upcommingGameData,
          isLive: true,
        });
        const user = getUserByJwt();
        this.setState({
          isLoading: false,
          user,
        });
      }
    } catch (err) {}
    const announcement = await http.get(apiEndPoint("api/announcement/"));
    this.setState({
      announcement: announcement.data.results,
      isLoading: false,
    });
  }

  compare = (a, b) => {
    if (a.id < b.id) {
      return -1;
    }
    if (a.id > b.id) {
      return 1;
    }
    return 0;
  };

  handleFilter = (name) => {
    const newData = [...this.state.data];
    const liveGameId = [...this.state.liveGameId];
    const upcommingGameId = [...this.state.upcommingGameId];
    const liveGameItems = [];
    const upcommingGameItems = [];
    for (var item of newData) {
      if (item.game_name.toLowerCase() === name.toLowerCase()) {
        if (liveGameId.includes(item.id)) {
          liveGameItems.push(item);
        } else if (upcommingGameId.includes(item.id)) {
          upcommingGameItems.push(item);
        }
      }
      if (name.toLowerCase() === "All Games".toLowerCase()) {
        if (liveGameId.includes(item.id)) {
          liveGameItems.push(item);
        } else if (upcommingGameId.includes(item.id)) {
          upcommingGameItems.push(item);
        }
      }
    }
    const currentGameName = { ...this.state.currentGameName };

    currentGameName.itemCount = liveGameItems.length;
    currentGameName.gameName = name;
    this.setState({
      currentGameData: liveGameItems,
      currentGameName,
      liveGameData: liveGameItems,
      upcommingGameData: upcommingGameItems,
      isLive: true,
    });
  };

  filteredForLive = (name) => {
    if (name.toLowerCase() === "upcomming") {
      this.setState({
        isLive: false,
        currentGameData: this.state.upcommingGameData || {},
      });
    } else {
      this.setState({
        isLive: true,
        currentGameData: this.state.liveGameData || {},
      });
    }
  };

  render() {
    const { isLoading, isLive, user } = this.state;

    return (
      <>
        {user && !user.admin && (
          <div
            className="liveBox2 container mb-2"
            style={{ backgroud: "white" }}
          >
            <span
              className="LiveBtn2"
              data-toggle="modal"
              data-target="#WithDeaw"
            >
              WithDraw
            </span>
            <span
              className="LiveBtn2 "
              data-toggle="modal"
              data-target="#Deposit"
            >
              Deposit
            </span>
          </div>
        )}
        {isLoading && <Loader />}
        {!isLoading && (
          <section className="section section-sm ">
            <div className="container">
              <div className="gamesSliderBox">
                <Swiper
                  breakpoints={{
                    320: {
                      slidesPerView: 3,
                      spaceBetween: 0,
                    },

                    480: {
                      slidesPerView: 4,
                      spaceBetween: 0,
                    },

                    640: {
                      slidesPerView: 5,
                      spaceBetween: 0,
                    },
                  }}
                >
                  {this.state.allData.map(({ name, id }) => {
                    return (
                      <SwiperSlide key={id} className="sprite  icon">
                        <span
                          className={
                            this.state.currentGameName.gameName.toLowerCase() ===
                            name.toLocaleLowerCase()
                              ? "sprite active icon"
                              : " sprite  icon"
                          }
                          onClick={() => this.handleFilter(name.toLowerCase())}
                        >
                          {name !== "Cricket" && (
                            <span
                              className={"sprite icofont-" + name.toLowerCase()}
                            />
                          )}
                          {(name === "Others" || name === "All Games") && (
                            <span className="sprite icofont-bird-wings " />
                          )}
                          {name === "Cricket" && (
                            <span className="sprite icofont-helmet" />
                          )}
                          <span className="isotope-filters-list-text">
                            {name}
                          </span>
                        </span>
                      </SwiperSlide>
                    );
                  })}
                </Swiper>
              </div>
              <div className="">
                <div className="liveBox2">
                  <span
                    className={
                      !isLive
                        ? "LiveBtn2 container p-0 m-0"
                        : "LiveBtn2 container p-0 m-0 actived"
                    }
                    onClick={() => this.filteredForLive("live")}
                  >
                    Live Match
                  </span>
                  <span
                    className={
                      isLive
                        ? "LiveBtn2 container p-0 m-0"
                        : "LiveBtn2 container p-0 m-0 actived"
                    }
                    onClick={() => this.filteredForLive("upcomming")}
                  >
                    Upcoming Match
                  </span>
                </div>
              </div>
              <span className="announceBox ">
                <span className="marquee">
                  {this.state.announcement &&
                    this.state.announcement.map(({ id, text, expired }) => {
                      return <p>{!expired && <span key={id}>{text}</span>}</p>;
                    })}
                </span>
              </span>
              <div className="accordion" id="accordion">
                {this.state.currentGameData &&
                  this.state.currentGameData.map(
                    ({ game_name, title, start_time, id, hide }) => {
                      return (
                        <>
                          {!hide && (
                            <div
                              className="accordion"
                              key={id.toString() + "*" + game_name}
                            >
                              <div
                                className="sport-table-header"
                                id="headingOne"
                              >
                                <span className="mb-0">
                                  <span
                                    data-toggle="collapse"
                                    data-target={
                                      "#collapseOne" + id.toString() + game_name
                                    }
                                    aria-expanded="true"
                                    aria-controls="collapseOne"
                                  >
                                    <div className="row">
                                      <div className="col-xl-1 col-lg-1 col-md-1 col-sm-2 col-xs-2">
                                        <div className="sport-table-icon">
                                          <span
                                            className={
                                              "sprite icofont-bird-wings"
                                            }
                                          />
                                        </div>
                                      </div>
                                      <div className="col-xl-11 col-lg-11 col-md-11 col-sm-10 col-xs-10">
                                        <span
                                          style={{
                                            color: "",
                                            fontWeight: "bold",
                                            display: "inline-block",
                                          }}
                                        >
                                          <span style={{ color: "red" }}>
                                            {game_name}
                                          </span>{" "}
                                          : {title}
                                        </span>

                                        <br />
                                        <span style={{ color: "#999" }}>
                                          {start_time.split("T")[0]}{" "}
                                          {start_time
                                            .split("T")[1]
                                            .split("+")[0]
                                            .substring(0, 5)}
                                        </span>
                                      </div>
                                    </div>
                                  </span>
                                </span>
                              </div>
                              <div
                                id={"collapseOne" + id.toString() + game_name}
                                className="collapse show"
                                aria-labelledby="headingOne"
                                data-parent="#accordion"
                              >
                                <div className="panel-body">
                                  <div className="accordion" id="accordion2">
                                    {this.state.matchesQuestion[id] &&
                                      this.state.matchesQuestion[id].map(
                                        ({
                                          question,
                                          option_1,
                                          option_2,
                                          winner,
                                          ...restProps
                                        }) => {
                                          if (winner) {
                                            return null;
                                          }
                                          return (
                                            <InPlayHelper
                                              key={
                                                id.toString() +
                                                question +
                                                option_1 +
                                                option_1 +
                                                option_2
                                              }
                                              {...restProps}
                                              option_1={option_1}
                                              option_2={option_2}
                                              question={question}
                                              is_live={this.state.isLive}
                                            />
                                          );
                                        }
                                      )}
                                  </div>
                                </div>
                              </div>
                            </div>
                          )}
                          {!start_time && (
                            <div>
                              <h1>No data found</h1>
                            </div>
                          )}
                        </>
                      );
                    }
                  )}
              </div>
            </div>
          </section>
        )}
        <Deposite />
        <WithDeaw />
      </>
    );
  }
}

export default HomePage;
