import React, { Component } from "react";
import http from "../services/httpServices";
import { apiEndPoint } from "../endPoins";

import Pagination from "../profile/paginatin";
import { paginate } from "../profile/paginationHelper";
import Loader2 from "../loader copy";

class Histry extends Component {
  state = {
    data: [],
    currentPage: 1,
    pageSize: 10,
    currentShowData: [],
    isLoading: true,
  };

  async componentDidMount() {
    try {
      var { data } = await http.get(apiEndPoint("api/bet_scope/?limit=10000"));
      data = data.results.sort(this.compare);

      var j = 1;
      const data1 = [];
      for (var item of data) {
        if (item.winner) {
          item.serial = j;
          item.winner = item[item.winner];
          data1.push(item);
          console.log(item);
          j += 1;
        }
      }

      this.setState({
        data: data1,
        currentShowData: data1.slice(0, this.state.pageSize),
      });
    } catch (err) {}
    this.setState({ isLoading: false });
  }

  compare = (a, b) => {
    if (a.id < b.id) {
      return -1;
    }
    if (a.id > b.id) {
      return 1;
    }
    return 0;
  };
  handlePageChange = (page) => {
    const { data, pageSize } = this.state;
    const currentShowData = paginate(data, page, pageSize);
    this.setState({ currentPage: page, currentShowData });
  };

  handlePageChange2 = (value) => {
    var { data, pageSize, currentPage } = this.state;
    currentPage += value;
    const currentShowData = paginate(data, currentPage, pageSize);
    this.setState({ currentPage, currentShowData });
  };
  render() {
    const { data, currentPage, pageSize, currentShowData, isLoading } =
      this.state;

    return (
      <>
        {isLoading && <Loader2 />}
        {!isLoading && (
          <div>
            <div className="m-0">
              <div style={{ overflowX: "scroll" }}>
                <table
                  style={{
                    overflowX: "scroll",
                    margin: "0px",
                  }}
                  className="table m-0 table-dark table-striped table-bordered"
                >
                  <thead>
                    <tr>
                      <td>Serial</td>
                      <td>Match Name</td>
                      <td>Question</td>
                      <td>winner</td>
                    </tr>
                  </thead>
                  <tbody>
                    {currentShowData.map(
                      (
                        {
                          match_name,
                          choice,
                          id,
                          serial,
                          match_start_time,
                          question,
                          winner,
                        },
                        index
                      ) => {
                        return (
                          <tr key={id.toString() + index.toString()}>
                            <td>{serial}</td>
                            <td>
                              {match_name}
                              <br />
                              {match_start_time &&
                                match_start_time.split(" ")[0]}{" "}
                              <br />
                              {match_start_time &&
                                match_start_time.split(" ")[1].split("+")[0]}
                            </td>
                            <td>{question}</td>

                            <td>{winner}</td>
                          </tr>
                        );
                      }
                    )}
                    {!data && <span>No data fount</span>}
                  </tbody>
                </table>
              </div>
              <Pagination
                onPageChange2={this.handlePageChange2}
                itemsCount={data.length}
                pageSize={pageSize}
                currentPage={currentPage}
                onPageChange={this.handlePageChange}
              />
            </div>
          </div>
        )}
      </>
    );
  }
}

export default Histry;
