import React, { Component } from "react";
import { apiEndPoint } from "../endPoins";
import { getUserByJwt, getJwt } from "../services/authServices";
import axios from "axios";
import Error404 from "../404";
axios.defaults.headers.common["x-auth-token"] = getJwt();

class Sponsor extends Component {
  state = {
    tableHead: [
      { id: "1", header: "SN" },
      { id: "2", header: "Name" },
      { id: "3", header: "UseName" },
    ],
    tableData: [
      {
        first_name: "",
        last_name: "",
        username: "",
        serial: "",
      },
    ],
    userDetails: {},
  };
  async componentDidMount() {
    try {
      const userDetails = getUserByJwt();
      this.setState({ userDetails });
    } catch (err) {}

    const dataOfUser = await axios.get(apiEndPoint("api/user-detail-update/"));
    const { refer_set } = dataOfUser.data;
    refer_set.map((item, index) => {
      item.serial = index + 1;
      return null;
    });

    this.setState({
      tableData: refer_set,
      isLoading: false,
    });
  }
  render() {
    return (
      <>
        {this.state.userDetails && (
          <div className="mt-4 container">
            <br />

            <div className="gamesSliderBox">
              <span className="sssiloyIconBox sprite isotopeIconBox pt-2">
                <span className=" icofont-chart-flow-1 mb-1" />
                <p>Sponsored By You</p>
              </span>
            </div>
            <table
              className="table table-dark table-striped "
              style={{ minWidth: "100%" }}
            >
              <thead>
                <tr>
                  {this.state.tableData.length!==0 && this.state.tableHead.map(({ id, header }) => {
                    return (
                      <th scope="col" key={id}>
                        {header}
                      </th>
                    );
                  })}
                </tr>
              </thead>
              <tbody>
                {this.state.tableData.map(
                  ({ first_name, serial, username, last_name },index) => {
                    return (
                      <tr key={index}>
                        <td>{serial}</td>
                        <td>
                          {first_name} {last_name}
                        </td>
                        <td>{username}</td>
                      </tr>
                    );
                  }
                )}
              </tbody>
            </table>
            {this.state.tableData.length === 0 && (
              <div className="container text-center">
                <h5>You have not Refered any one !</h5>
              </div>
            )}
          </div>
        )}
        {!this.state.userDetails && <Error404 />}
      </>
    );
  }
}

export default Sponsor;
