import React, { Component } from "react";
import Footer from "./footer/footer";
import HomePage from "./homePage/homePage";
import Navbar from "./navBar/navBar";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Login from "./login/login";
import Register from "./login/register";
import Profile from "./profile/profile";
import Histry from "./gameEditor/histry";
import AllTransactions from "./profile/allTransactions";
import Deposite from "./login/deposite";
import WithDeaw from "./login/withDraw";
import SeacialNavbar from "./footer/speacialNavbar";
import Transfer from "./login/transfer";
import Sponsor from "./statement/sponser";
import Logout from "./login/logout";
import { ToastContainer } from "react-toastify";
import { getUserByJwt } from "./services/authServices";
import LiveGameEditor from "./gameEditor/gameEditor";
import Error404 from "./404";
import ClubUser from "./club/clubUser";
import ClubProfile from "./club/clubProfile";
import StatementClub from "./club/statement";

class App extends Component {
  state = {
    user: {
      email: null,
      first_name: null,
      game_editor: null,
      id: null,
      is_superuser: null,
      last_name: null,
      phone: null,
      referred_by: null,
      username: null,
      admin: null,
    },
  };
  componentDidMount() {
    try {
      const user = getUserByJwt();
      if (user) {
        this.setState({ user });
      }
    } catch (err) {}
  }
  render() {
    const { id } = this.state.user;

    return (
      <Router>
        <div className="" style={{ minHeight: "100vh", background: "" }}>
          <Navbar userId={id} />

          <Switch>
            <Route path="/profile">
              <Profile />
            </Route>

            <Route path="/allTransactions" exact={true}>
              <AllTransactions />
            </Route>
            <Route path="/StatementClub">
              <StatementClub />
            </Route>

            <Route path="/logout">
              <Logout />
            </Route>
            <Route path="/liveGameeditor">
              <LiveGameEditor />
            </Route>
            <Route path="/user">
              <ClubUser />
            </Route>
            <Route path="/" exact={true}>
              <HomePage />
            </Route>
            <Route path="/sponsor">
              <Sponsor />
            </Route>
            <Route path="/History">
              <Histry />
            </Route>
            <Route path="/clubprofile">
              <ClubProfile />
            </Route>
            <Route path="/">
              <Error404 />
            </Route>
          </Switch>
          <Login />
          <Register />
          <Deposite />
          <WithDeaw />
          <Transfer />
        </div>
        <Footer />
        <ToastContainer />
        {this.state.user.id && <SeacialNavbar />}
      </Router>
    );
  }
}

export default App;
