import React from "react";
import { Input } from "./inputHelp";
import { apiEndPoint } from "../endPoins";
import { getJwt } from "../services/userService";
import TransactionForm from "./transaction";
import Joi from "joi-browser";
import http from "../services/httpServices";
import { toast } from "react-toastify";
http.setJwt(getJwt());
class WithDeaw extends TransactionForm {
  state = {
    data: {
      type: "withdraw",

      method: "",

      amount: "0",

      account: "",
    },
    errors: {
      method: null,
      amount: null,
      type: null,
      account: null,
    },
    type: [{ id: "1", option: "Personal" }],
    methodOption: [
      {
        id: "bkash",
        to_show: "BKash personal",
      },
    ],
  };
  schema = {
    method: Joi.string().required().label("Method"),
    amount: Joi.number().min(100).required().label("Amount"),
    account: Joi.string().min(11).max(15).required().label("Account"),
    type: Joi.string().required(),
  };
  async componentDidMount() {
    try {
      const res = await http.get(
        apiEndPoint("api/transactions/available_methods/")
      );
      this.setState({ methodOption: res.data.methods });
      const target = res.data.methods[0]["id"];
      this.handleClickOfoption(target);
    } catch (e) {
      toast.error("Method Option Error");
    }
  }

  handleClickOfoption = ({ value }) => {
    const res = this.getNumbersFromId(value, this.state.methodOption);
    if (res) {
      this.setState({ numbers: res });
    }
  };

  getNumbersFromId = (id, data) => {
    for (let d of data) {
      if (d.id === id) return d.numbers;
    }
    return null;
  };
  
  doSubmit = async (e) => {
    try {
      await http.post(apiEndPoint("api/withdraw/"), this.state.data);
      toast.success("Your request has been success");
      window.location = "/profile";
    } catch (err) {
      if (err.response && err.response.status === 400) {
        const errors = { ...this.state.errors };
        try {
          errors.amount = err.response.data.non_field_errors[0];
        } catch (err) {}
        try {
          errors.amount = err.response.data.amount[0];
        } catch (err) {}
        this.setState({ errors });
      }
    }
  };
  render() {
    const { amount, account } = this.state.data;
    const { methodOption, errors } = this.state;
    return (
      <>
        <div className="modal fade" tabIndex="-1" role="dialog" id="WithDeaw">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header" style={{ background: "green" }}>
                <h5 className="modal-title">Withdraw</h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span className="icofont-ui-close btn-my"></span>{" "}
                </button>
              </div>
              <div className="modal-body">
                <form
                  className="rd-form rd-mailform"
                  onSubmit={this.handleSubmit}
                >
                  <div className="form-input form-group mb-2">
                    <label htmlFor="exampleFormControlSelect1">
                      Select Method
                    </label>
                    <select
                      className="form-control"
                      name="method"
                      onChange={this.handleChange}
                    >
                      <option key="N/A"> Select Method</option>
                      {methodOption.map(({ id, to_show }) => {
                        return (
                          <option value={id} key={id}>
                            {id}
                          </option>
                        );
                      })}
                    </select>
                    <small id="emailHelp" className="form-text text-warning">
                      {errors.method}
                    </small>
                  </div>
                  <div className="form-input form-group">
                    <label htmlFor="exampleFormControlSelect1">
                      Account Type
                    </label>
                    <select className="form-control">
                      <option key="N/A">Account Type</option>
                      {this.state.type.map(({ id, option }) => {
                        return (
                          <option
                            onClick={this.handleChange}
                            value={option}
                            key={id}
                          >
                            {option}
                          </option>
                        );
                      })}
                    </select>
                    <small id="emailHelp" className="form-text text-warning">
                      {errors.type}
                    </small>
                  </div>

                  <Input
                    placeholder="Enter Amount"
                    name="amount"
                    value={amount}
                    type="number"
                    min="100"
                    onChange={this.handleChange}
                    errors={errors.amount}
                  />
                  <Input
                    placeholder="To"
                    name="account"
                    value={account}
                    type="number"
                    onChange={this.handleChange}
                    errors={errors.account}
                  />

                  <button
                    className="button button-lg button-primary button-block"
                    type="submit"
                  >
                    WithDeaw
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default WithDeaw;
