import React from "react";
import PropTypes from "prop-types";
import _ from "lodash";

const Pagination = ({
  itemsCount,
  pageSize,
  currentPage,
  onPageChange,
  onPageChange2,
}) => {
  const pagesCount = Math.ceil(itemsCount / pageSize);
  if (pagesCount === 1) return null;
  const pages = _.range(1, pagesCount + 1);

  return (
    <nav style={{ zIndex: "1" }}>
      <ul className="pagination pt-4 pb-4 m-0">
        <li className="page-item">
          <button
            className="page-link"
            aria-label="Previous"
            onClick={() => onPageChange2(-1)}
            disabled={1 >= currentPage}
          >
            <span aria-hidden="true">&laquo;</span>
          </button>
        </li>
        {pages.slice(Math.max(0,currentPage-5), Math.min(currentPage + 4, pagesCount+1)).map((page) => (
          <li
            key={page}
            className={page === currentPage ? "page-item active" : "page-item"}
            style={{ zIndex: "1" }}
          >
            <span className="page-link" onClick={() => onPageChange(page)}>
              {page}
            </span>
          </li>
        ))}
        <li className="page-item">
          <button
            className="page-link"
            aria-label="Next"
            onClick={() => onPageChange2(1)}
            disabled={pagesCount <= currentPage}
          >
            <span aria-hidden="true">&raquo;</span>
          </button>
        </li>
      </ul>
    </nav>
  );
};

Pagination.propTypes = {
  itemsCount: PropTypes.number.isRequired,
  pageSize: PropTypes.number.isRequired,
  currentPage: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};

export default Pagination;
