import { apiEndPoint } from "../endPoins";
import http from "../services/httpServices";
export function register({
  username,
  password,
  phoneNumber,
  fullName,
  clubName,
  sponserName,
  email,
}) {
  return http.post(apiEndPoint("api/register/"), {
    username: username,
    phone: phoneNumber,
    password: password,
    first_name: fullName,
    user_club: clubName,
    email: email,
    referer_username: sponserName,
  });
}

export function login({ username, password }) {
  return http.post(apiEndPoint("api/login/"), {
    username,
    password,
  });
}

export function loginClub({ username, password }) {
  return http.post(apiEndPoint("api/login-club/"), {
    username,
    password,
  });
}

export function getJwt() {
  return localStorage.getItem("token");
}
