import React from "react";
import { apiEndPoint } from "../endPoins";
import { getJwt } from "../services/userService";
import { Input } from "./inputHelp";
import TransactionForm from "./transaction";
import Select from "react-select";
import http from "../services/httpServices";
import { toast } from "react-toastify";
import Loader2 from "../loader copy";
import {  getUserByJwt } from "../services/authServices";
http.setJwtForClub(getJwt());
http.setJwt(getJwt());

class Transfer extends TransactionForm {
  state = {
    data: {
      amount: "0",
      to: "",
    },
    options: [],
    errors: {},
    isLoading: false,
    isClub: false,
    is_club_admin: false,
  };

  handleSubmit = async (e) => {
    this.setState({ isLoading: true });
    e.preventDefault();


    try {
      if (this.state.isClub ) {
        await http.post(apiEndPoint("api/club_transfer/"), this.state.data);
        toast.success("Transfer success");
      } else {
        await http.post(apiEndPoint("api/transfer/"), this.state.data);
        toast.success("Transfer success");
      }

      this.setState({
        data: { amount: 0, to: this.state.data.to },
      });
    } catch (e) {
      try {
        toast.error(e.response.data.non_field_errors[0]);
      } catch (err) {
        toast.error("Unable to transfer money !");
      }
    }

    this.setState({ isLoading: false });
  };
  async componentDidMount() {
    try {
      try {
        const { is_club_admin, admin } = getUserByJwt();

        if (admin) {
          this.setState({ isClub: true });
        }
        if (is_club_admin) {
          this.setState({ is_club_admin: true });
        }
      } catch (err) {}

      const res = await http.get(apiEndPoint("api/user/?limit=10000"));
      const options = res.data.results;
      options.map((item) => {
        item.value = item.id;
        item.label = item.username;
        return null;
      });
      this.setState({ options });
    } catch (err) {}
  }
  handleChange2 = (selectedOption) => {
    const data = { ...this.state.data };
    data.to = selectedOption.id;

  
    this.setState({ data });
  };

  render() {
    const { amount } = this.state.data;
    const { errors, options, isLoading } = this.state;
    return (
      <>
        {isLoading && <Loader2 />}{" "}
        <div className="modal fade" tabIndex="-1" role="dialog" id="Transfer">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header" style={{ background: "green" }}>
                <h5 className="modal-title">Transfer</h5>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span className="icofont-ui-close btn-my"></span>
                </button>
              </div>
              <div className="modal-body">
                <form
                  className="rd-form rd-mailform"
                  onSubmit={this.handleSubmit}
                >
                  <div className="form-group ">
                    <label htmlFor="to" style={{ marginBottom: "10px" }}>
                      TO
                    </label>
                    <Select
                      options={options}
                      name="to"
                      placeholder="To"
                      onChange={this.handleChange2}
                    />
                  </div>
                  <Input
                    placeholder="Enter Amount"
                    name="amount"
                    value={amount}
                    onChange={this.handleChange}
                    type="number"
                    errors={errors.amount}
                    key={"amount1"}
                    min="10"
                  />
                  {/* <Input
                    placeholder="Enter Password"
                    name="password"
                    value={password}
                    onChange={this.handleChange}
                    type="password"
                    errors={errors.password}
                  /> */}
                  <button
                    className="button button-lg button-primary button-block"
                    type="submit"
                  >
                    Transfer
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Transfer;
