import React from "react";
import { Input } from "./inputHelp";
import LoginRegister from "./loginRegister";
import Joi from "joi-browser";
import { login, loginClub } from "../services/userService";
import { toast } from "react-toastify";

class Login extends LoginRegister {
  state = {
    data: {
      username: "",
      password: "",
    },
    isClub: false,
    errors: {
      username: null,
      password: null,
    },
  };

  schema = {
    username: Joi.string().min(3).max(8).required().label("User Name"),
    password: Joi.string().min(6).max(30).required().label("password"),
  };

  doSubmit = async (e) => {
    try {
      if (this.state.isClub) {
        const data = await loginClub(this.state.data);
        toast.success("Log in Successfull !");
        localStorage.setItem("token", data.data.jwt);
        this.setState({ data: {} });
        window.location = "/";
      } else {
        const data = await login(this.state.data);
        toast.success("Log in Successfull !");
        localStorage.setItem("token", data.data.jwt);
        this.setState({ data: {} });
        window.location = "/";
      }
    } catch (err) {
      if (err.response && err.response.status === 400) {
        const errors = { ...this.state.errors };
        errors.username = err.response.data.detail;
        this.setState({ errors });
  
      }
    }
  };

  render() {
    const { username, password } = this.state.data;
    const { errors } = this.state;

    return (
      <div className="modal fade" tabIndex="-1" role="dialog" id="Login">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header" style={{ background: "green" }}>
              <h5 className="modal-title">Login form</h5>
              <button
                type="button"
                className="close "
                data-dismiss="modal"
                aria-label="Close"
              >
                <span className="icofont-ui-close btn-my"></span>
              </button>
            </div>
            <div className="modal-body">
              <form
                className="rd-form rd-mailform"
                onSubmit={this.handleSubmit}
              >
                <Input
                  placeholder="Username"
                  name="username"
                  value={username}
                  onChange={this.handleChange}
                  errors={errors.username}
                />
                <Input
                  placeholder="Password"
                  name="password"
                  value={password}
                  onChange={this.handleChange}
                  type="password"
                  errors={errors.password}
                />
                <div className="form-check">
                  <input
                    type="checkbox"
                    className="form-check-input"
                    id="exampleCheck1"
                    value={this.state.isClub}
                    onClick={() =>
                      this.setState({ isClub: !this.state.isClub })
                    }
                  />
                  <label className="form-check-label" htmlFor="exampleCheck1">
                    Is Club ?
                  </label>
                </div>
                <button
                  className="button button-lg button-primary button-block mt-4"
                  type="submit"
                >
                  Login
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Login;
