import React, { Component } from "react";
import { apiEndPoint } from "../endPoins";
import { getJwt } from "../services/userService";
import { toast } from "react-toastify";
import Loader2 from "../loader copy";
import { Input } from "../login/inputHelp";
import http from "../services/httpServices";

http.setJwt(getJwt());

class CreateMatchQuestion extends Component {
  state = {
    data: {
      question: "",
      option_1: "",
      option_1_rate: 1,
      option_2: "",
      option_2_rate: 1,
      option_3: "",
      option_3_rate: 1,
      option_4: "",
      option_4_rate: 1,
      is_locked: "",
      id: "",
      is_live: "",
      match: "",
    },
    errors: {
      question: "",
      option_1: "",
      option_1_rate: "",
      option_2: "",
      option_2_rate: "",
      option_3: "",
      option_3_rate: "",
      option_4: "",
      option_4_rate: "",
      is_locked: "",
      id: "",
      is_live: "",
      match: "",
    },
    match: "",
    game_name: "",
    isDisable: false,
    game_editor: false,
  };

  componentDidMount() {
    const { match, game_name } = { ...this.props };
    
    this.setState({ match, game_name });
  }

  doSubmit = async (e) => {
    this.setState({ isDisable: true });
    e.preventDefault();
    try {
      const data = { ...this.state.data };
      const id = this.state.match;
      data.match = id;
      await http.post(apiEndPoint("api/bet_scope/"), data);
      toast.success("Question Created !");
      window.location="/livegameeditor"
    } catch (err) {
      try {
        toast.error(err.response.data.non_field_errors[0]);
      } catch (err) {}
    }
    this.setState({ isDisable: false });
  };

  handleChange = ({ currentTarget: input }) => {
    const data = { ...this.state.data };
    data[input.name] = input.value;
    this.setState({ data });
  };

  render() {
    const {
      question,
      option_1,
      option_1_rate,
      option_2,
      option_2_rate,
      option_3,
      option_3_rate,
      option_4,
      option_4_rate,
    } = this.state.data;
    const { errors, match, game_name } = this.state;

    return (
      <>
        {" "}
        {this.isDisable && <Loader2 />}
        {match && (
          <div
            className="modal fade"
            id={(
              "CreateMatchQuestion" +
              match.toString() +
              game_name.trim()
            ).trim()}
            tabIndex="-1"
            role="dialog"
          >
     
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header" style={{ background: "green" }}>
                  <h5 className="modal-title">Add question {game_name}</h5>
                  <button type="button" data-dismiss="modal" aria-label="Close">
                    <span className="icofont-ui-close btn-my"></span>
                  </button>
                </div>
                <div className="justify-content-center modal-body">
                  <form className="form-group" onSubmit={this.doSubmit}>
                    <Input
                      placeholder="Enter question"
                      name="question"
                      value={question}
                      onChange={this.handleChange}
                      errors={errors.fullName}
                    />

                    <Input
                      name="option_1"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_1}
                      placeholder="Enter Option 1"
                    />
                    <Input
                      name="option_1_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_1_rate}
                      placeholder="Enter Option 1 rate"
                      type="number"
                      min="1.00"
                      step="0.01"
                    />
                    <Input
                      name="option_2"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_2}
                      placeholder="Enter Option 2"
                    />
                    <Input
                      name="option_2_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_2_rate}
                      placeholder="Enter Option 2 Rate"
                      type="number"
                      min="1.00"
                      step="0.01"
                    />
                    <Input
                      name="option_3"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_3}
                      placeholder="Enter Option 3"
                    />
                    <Input
                      name="option_3_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_3_rate}
                      placeholder="Enter Option 3 Rate"
                      type="number"
                      min="1.00"
                      step="0.01"
                    />
                    <Input
                      name="option_4"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_4}
                      placeholder="Enter Option 4"
                    />
                    <Input
                      name="option_4_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_4_rate}
                      placeholder="Enter Option 4 Rate"
                      type="number"
                      min="1.00"
                      step="0.01"
                    />
                    <button
                      className="button button-lg button-primary button-block"
                      type="submit"
                    >
                      Add Question
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        )}
        <></>
      </>
    );
  }
}

export default CreateMatchQuestion;
