import React, { Component } from "react";
import LiveDot from "../homePage/liveDot";
import axios from "axios";
import BetUpdate from "./popUp";
import { apiEndPoint } from "../endPoins";
import { toast } from "react-toastify";
import Winner from "./winner";

class GameHelper extends Component {
  state = {
    data: {},
  };
  componentDidMount() {
    this.setState({ data: { ...this.props } });
  }
  onClickHide = async (e) => {
    try {
      const { hide, id } = this.state.data;
      const value = !(hide === true);
      await axios.patch(apiEndPoint("api/bet_scope/" + id + "/"), {
        hide: value,
      });
      toast.success("Question Update !");
      const data = { ...this.state.data };
      data.hide = value;
      this.setState({ data });
    } catch (err) {
      toast.error("An error occour");
    }
  };
  onClickLock = async (e) => {
    try {
      const { locked, id } = this.state.data;
      const value = !(locked === true);
      await axios.patch(apiEndPoint("api/bet_scope/" + id + "/"), {
        locked: value,
      });
      toast.success("Question Update !");
      const data = { ...this.state.data };
      data.is_locked = value;
      this.setState({ data });
    } catch (err) {
      toast.error("An error occour");
    }
  };
  render() {
    const {
      question,
      option_1,
      option_1_rate,
      option_2,
      option_2_rate,
      option_3,
      option_3_rate,
      option_4,
      option_4_rate,
      is_locked,
      id,
      is_live,
      hide,
      winner,
    } = this.state.data;

    const {
      option1_benefit,
      option1_bet,
      option1_bet_count,
      option2_benefit,
      option2_bet,
      option2_bet_count,
      option3_benefit,
      option3_bet,
      option3_bet_count,
      option4_benefit,
      option4_bet,
      option4_bet_count,
    } = this.props.details;

    return (
      <>
        {id && !winner && (
          <>
            <div className={is_locked ? " " : ""}>
              <div className="accordion" id="accordionExample">
                <div className="sport-table-header2">
                  <div className={"sport-table-header2 "}>
                    <span className="">
                      {is_locked && <span className="icofont-lock h4"></span>}
                      {!is_locked && (
                        <span className="icofont-ui-unlock h4" />
                      )}{" "}
                      {!hide && <span className="icofont-eye-open h4" />}
                      {hide && <span className="icofont-eye-blocked h4" />}{" "}
                      {question}
                      {is_live && (
                        <>
                          <span className="bg">
                            LIVE <LiveDot />
                          </span>
                        </>
                      )}
                      {!is_live && (
                        <>
                          <span className="bg ">
                            UPCOMING <LiveDot />
                          </span>
                        </>
                      )}
                      <span
                        data-toggle="modal"
                        data-target={"#sportModal" + id.toString().trim()}
                        className="liveBox2 LiveBtn2 mr-2 ml-3 p-1"
                      >
                        Update
                      </span>
                      {hide && (
                        <span
                          onClick={() => this.onClickHide()}
                          className="liveBox2 LiveBtn2 mr-2 ml-3 p-1"
                        >
                          unhide
                        </span>
                      )}
                      {!hide && (
                        <span
                          onClick={() => this.onClickHide()}
                          className="liveBox2 LiveBtn2 mr-2 ml-3 p-1"
                        >
                          hide
                        </span>
                      )}
                      {is_locked && (
                        <span
                          onClick={() => this.onClickLock()}
                          className="liveBox2 LiveBtn2 mr-2 ml-3 p-1"
                        >
                          unlock
                        </span>
                      )}
                      {!is_locked && (
                        <span
                          onClick={() => this.onClickLock()}
                          className="liveBox2 LiveBtn2 mr-2 ml-3 p-1"
                        >
                          lock
                        </span>
                      )}
                      <Winner
                        option_1={option_1}
                        option_2={option_2}
                        option_3={option_3}
                        option_4={option_4}
                        betId={id}
                      />
                    </span>
                  </div>

                  <div className="accordion-collapse collapse show">
                    <div className="panel-body" style={{ overflow: "scroll" }}>
                      <table className="table table-hover table-dark">
                        <thead>
                          <tr>
                            <th scope="col">Answer</th>
                            <th scope="col">Bet Rate</th>
                            <th scope="col">Place bet</th>
                            <th scope="col">Betting Amount</th>
                            <th scope="col">Revenu</th>
                          </tr>
                        </thead>
                        <tbody>
                          {option_1 && (
                            <tr>
                              <td>{option_1}</td>
                              <td className=" ">
                                <p className="sport-table-wager-button-count">
                                  {option_1_rate}
                                </p>
                              </td>
                              <td>{option1_bet_count}</td>
                              <td>{option1_bet}</td>
                              <td>{option1_benefit}</td>
                            </tr>
                          )}
                          {option_2 && (
                            <tr>
                              <td>{option_2}</td>
                              <td className=" poiner ">
                                <span className="sport-table-wager-button-count">
                                  {option_2_rate}
                                </span>
                              </td>
                              <td>{option2_bet_count}</td>
                              <td>{option2_bet}</td>
                              <td>{option2_benefit}</td>
                            </tr>
                          )}
                          {option_3 && (
                            <tr>
                              <td>{option_3}</td>
                              <td className="">
                                <span className="sport-table-wager-button-count">
                                  {option_3_rate}
                                </span>
                              </td>
                              <td>{option3_bet_count}</td>
                              <td>{option3_bet}</td>
                              <td>{option3_benefit}</td>
                            </tr>
                          )}
                          {option_4 && (
                            <tr>
                              <td>{option_4}</td>
                              <td className="">
                                <span className="sport-table-wager-button-count">
                                  {option_4_rate}
                                </span>
                              </td>
                              <td>{option4_bet_count}</td>
                              <td>{option4_bet}</td>
                              <td>{option4_benefit}</td>
                            </tr>
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <BetUpdate
              question={question}
              option_1={option_1}
              option_1_rate={option_1_rate}
              option_2={option_2}
              option_2_rate={option_2_rate}
              option_3={option_3}
              option_3_rate={option_3_rate}
              option_4={option_4}
              option_4_rate={option_4_rate}
              is_locked={is_locked}
              id={id}
              is_live={is_live}
              hide={hide}
              winner={winner}
            />
          </>
        )}
      </>
    );
  }
}

export default GameHelper;
