import React, { Component } from "react";
import "./navBar.css";
import { Link } from "react-router-dom";
import {
  getCurrentUser,
  getUserByJwt,
  getCurrentClub,
} from "../services/authServices";

class Navbar extends Component {
  state = {
    user: {
      username: "",
      balance: "",
      id: "",
      name: "",
      game_editor: false,
      admin: false,
    },
    currentUserData: {
      balance: "",
      username: "",
      id: "",
      name: "",
      game_editor: false,
      admin: false,
    },
  };
  async componentDidMount() {
    try {
      const data = getUserByJwt();

      if (data) {
        this.setState({
          user: data,
          currentUserData: data,
        });
      }
      try {
        const res = await getCurrentClub();
        this.setState({ currentUserData: res.data });
      } catch (err) {
        const res = await getCurrentUser();
        this.setState({ currentUserData: res.data });
      }
    } catch (e) {}
  }

  render() {
    const { username, name } = this.state.user;
    const { balance } = this.state.currentUserData;

    return (
      <>
        <nav className="myNavbar red">
          <ul
            className="inLine"
            style={{ width: "100%", background: "#000000", height: "60px" }}
          >
            <li>
              <Link
                to="./"
                className="icofont-elk"
                style={{ fontSize: "40px" }}
              >
                win24
              </Link>
            </li>
            {!name && !username && (
              <>
                <li style={{ float: "right" }}>
                  <div
                    className="white"
                    data-toggle="modal"
                    data-target="#Login"
                  >
                    Login
                  </div>
                </li>
                <li style={{ float: "right" }}>
                  <div
                    className="white"
                    data-toggle="modal"
                    data-target="#Register"
                  >
                    Register
                  </div>
                </li>
              </>
            )}
            {(name && (
              <>
                <li style={{ float: "right", fontSize: "15px" }}>
                  <Link className="white" to="/clubprofile">
                    {username}
                    {name}
                    <span className="icofont-taka">
                      {balance && balance.toFixed(2)}
                    </span>
                  </Link>
                </li>
              </>
            ))}
            {(username) && (
              <>
                <li style={{ float: "right", fontSize: "15px" }}>
                  <Link className="white" to="/profile">
                    {username}
                    {name}
                    <span className="icofont-taka">
                      {balance && balance.toFixed(2)}
                    </span>
                  </Link>
                </li>
              </>
            )}
          </ul>
        </nav>
      </>
    );
  }
}

export default Navbar;
