import React, { Component } from "react";
import OriginalProfile from "./onriginalProfile";
import NavBarProfile from "./navBarProfile";
import { getUserByJwt } from "../services/authServices";
import Error404 from "../404";

class Profile extends Component {
  state = {
    user: {},
  };
  componentDidMount() {
    try {
      const user = getUserByJwt();
      this.setState({ user });
    } catch (err) {}
  }
  render() {
    const { user } = this.state;
 
    return (
      <>
        {user && !user.admin && (
          <>
            <NavBarProfile />
            <OriginalProfile />
          </>
        )}
        {(!user || user.admin) && <Error404 />}
      </>
    );
  }
}

export default Profile;
