import React from "react";
import LoginRegister from "./loginRegister";
import { Input } from "./inputHelp";
import { register } from "../services/userService";
import axios from "axios";
import Joi from "joi-browser";
import { toast } from "react-toastify";
import { apiEndPoint } from "../endPoins";
class Register extends LoginRegister {
  state = {
    data: {
      username: "",
      password: "",
      phoneNumber: "",
      fullName: "",
      clubName: "",
      sponserName: "",
      reTypepassword: "",
      email: "",
    },
    errors: {
      username: "",
      password: "",
      phoneNumber: "",
      fullName: "",
      clubName: "",
      sponserName: "",
      reTypepassword: "",
      email: "",
    },
    allClube: [{ name: "", id: "" }],
  };

  schema = {
    fullName: Joi.string().min(6).max(14).required().label("Full Name"),
    username: Joi.string()
      .alphanum()
      .min(3)
      .max(8)
      .required()
      .label("User Name"),
    email: Joi.string().required().email().label("Email"),
    password: Joi.string().alphanum().min(6).required().label("password"),
    phoneNumber: Joi.string().min(11).max(11).required().label("Phone Number"),
    clubName: Joi.string().label("Club Name"),
    reTypepassword: Joi.string().label("password"),
    sponserName: Joi.string().min(0).max(8).label("Sponser Name").allow(""),
  };
  compare = (a, b) => {
    if (a.id < b.id) {
      return -1;
    }
    if (a.id > b.id) {
      return 1;
    }
    return 0;
  };
  async componentDidMount() {
    const promise = await axios.get(apiEndPoint("api/club/"));
    var data = promise.data.results;
    data = data.sort(this.compare);
    this.setState({ allClube: data });
  }

  doSubmit = async (e) => {
    try {
      if (this.state.data.password !== this.state.data.reTypepassword) {
        const errors = { ...this.state.errors };
        errors.reTypepassword = "password does not match !";
        this.setState({ errors });
        return;
      }
      const data = await register(this.state.data);
      localStorage.setItem("token", data.data.jwt);
      this.setState({ data: {} });
      window.location = "/";
    } catch (err) {
      console.log(err.response);
      if (err.response && err.response.status === 400) {
        toast.error(err.response.data);
        const errors = { ...this.state.errors };
        try {
          errors.username = err.response.data.username[0];
        } catch (ex) {}
        try {
          errors.email = err.response.data.email[0];
        } catch (ex) {}
        try {
          errors.phoneNumber = err.response.data.phone[0];
        } catch (err) {}
        this.setState({ errors });
      }
    }
  };

  render() {
    const {
      username,
      password,
      phoneNumber,
      fullName,
      sponserName,
      reTypepassword,
      email,
    } = this.state.data;
    const { errors } = this.state;
    return (
      <>
        <div className="modal fade" id="Register" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header" style={{ background: "green" }}>
                <h5 className="modal-title">Register form</h5>
                <button type="button" data-dismiss="modal" aria-label="Close">
                  <span className="icofont-ui-close btn-my"></span>
                </button>
              </div>
              <div className="modal-body">
                <form
                  className="rd-form rd-mailform"
                  onSubmit={this.handleSubmit}
                >
                  <Input
                    placeholder="Enter Full Name"
                    name="fullName"
                    value={fullName}
                    onChange={this.handleChange}
                    errors={errors.fullName}
                  />
                  <Input
                    placeholder="Enter Phone Number"
                    name="phoneNumber"
                    value={phoneNumber}
                    type="number"
                    onChange={this.handleChange}
                    errors={errors.phoneNumber}
                  />
                  <Input
                    placeholder="Enter username"
                    name="username"
                    value={username}
                    onChange={this.handleChange}
                    errors={errors.username}
                  />
                  <Input
                    placeholder="Enter Email"
                    name="email"
                    value={email}
                    onChange={this.handleChange}
                    errors={errors.email}
                  />
                  <Input
                    placeholder="Enter password"
                    name="password"
                    value={password}
                    onChange={this.handleChange}
                    type="password"
                    errors={errors.password}
                  />
                  <Input
                    placeholder="Retype password"
                    name="reTypepassword"
                    value={reTypepassword}
                    onChange={this.handleChange}
                    errors={errors.reTypepassword}
                  />{" "}
                  <Input
                    placeholder="Enter Sponser Name"
                    name="sponserName"
                    value={sponserName}
                    onChange={this.handleChange}
                    errors={errors.sponserName}
                  />
                  <div className="form-input form-group mb-4">
                    <label htmlFor="exampleFormControlSelect1">
                      Select Club
                    </label>

                    <select
                      name="clubName"
                      onChange={this.handleChange}
                      className="form-control"
                      value={this.state.data.clubName}
                    >
                      <option key="n/a">Select Club</option>
                      {this.state.allClube.map(({ id, name }) => {
                        return (
                          <option value={id} key={id}>
                            {name}
                          </option>
                        );
                      })}
                    </select>
                    <small className="text-warning">{errors.clubName}</small>
                  </div>
                  <button
                    className="button button-lg button-primary button-block"
                    type="submit"
                  >
                    Singup
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Register;
