import React, { Component } from "react";
import { apiEndPoint } from "../endPoins";
import { getJwt } from "../services/authServices";
import http from "../services/httpServices";

http.setJwtForClub(getJwt());
http.setJwt(getJwt());

class Notification extends Component {
  state = { data: [] };
  async componentDidMount() {
    try {
      const res = await http.get(apiEndPoint("api/notification/"));
      const data = res.data.results;
      data.map((data, index) => {
        data.serial = index + 1;
        return null;
      });
      this.setState({ data });
    } catch (err) {}
  }

  render() {
    const { data } = this.state;
    return (
      <div className="modal" tabIndex="-1" role="dialog" id="Notifications">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title ">Notifications</h5>
              <button
                type="button"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span className="icofont-ui-close btn-my"></span>{" "}
              </button>
            </div>
            <div className="modal-body">
              {data.length > 0 && (
                <div>
                  <table className="table table-light" width="100vw">
                    <thead>
                      <tr>
                        <th scope="col">#</th>
                        <th scope="col">Description</th>
                        <th scope="col">Send At</th>
                      </tr>
                    </thead>
                    <tbody>
                      {data
                        .slice(0, 10)
                        .map(({ serial, message, created_at }, index) => {
                          return (
                            <tr key={index}>
                              <th scope="row">{serial}</th>
                              <td>{message}</td>
                              <td>{created_at.split("T")[0]}</td>
                            </tr>
                          );
                        })}
                    </tbody>
                  </table>
                </div>
              )}
              {data.length === 0 && (
                <h5 className="text-primary text-center h5">No Notification</h5>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Notification;
