import React, { Component } from "react";
import LiveDot from "./liveDot";
import { apiEndPoint } from "../endPoins";
import { getJwt } from "../services/userService";
import { toast } from "react-toastify";
import Loader2 from "../loader copy";
import http from "../services/httpServices";
import { getUserByJwt } from "../services/authServices";
http.setJwtForClub(getJwt());
http.setJwt(getJwt());
class BitPopUP extends Component {
  state = {
    detailsOfBet: {
      currentBettingRate: "",
      question: "",
      betFor: "",
      id: "",
      option: "",
    },

    userDetails: {
      userId: "",
      userBettingAmount: 0,
      userWinningPrice: 0,
    },
    someOption: [
      { id: 1, value: 100 },
      { id: 2, value: 200 },
      { id: 3, value: 500 },
      { id: 4, value: 1000 },
      { id: 5, value: 10000 },
    ],
    isDisable: false,
  };

  componentDidMount() {
    const res = this.props;
    this.setState({ detailsOfBet: res });
  }

  doSubmit = async (e) => {
    e.preventDefault();

    try {
      const { email } = getUserByJwt();

      if (!email) {
        toast.error("You are not allow to bet");
        return;
      }
    } catch (err) {}

    this.setState({ isDisable: true });

    try {
      const { userBettingAmount } = this.state.userDetails;
      const { id, option } = this.state.detailsOfBet;
      await http.post(apiEndPoint("api/bet/"), {
        bet_scope: id,
        choice: option,
        amount: userBettingAmount,
      });

      toast.success("Your Bet place Has Success");
    } catch (err) {
      try {
        try {
          toast.error(err.response.data.bet_scope[0]);
        } catch (err) {}
        try {
          toast.error(err.response.data.non_field_errors[0]);
        } catch (err) {}
        try {
          toast.error(err.response.data.detail);
        } catch (err) {}
      } catch (err) {
        toast.error("Maybe You are not Loged in !");
      }
    }
    this.setState({ isDisable: false });
  };

  handleChange = (e) => {
    const userDetails = { ...this.state.userDetails };
    let newBet = Math.floor(e.currentTarget.value);
    if (newBet < 0) {
      newBet = 0;
    }
    if (newBet > 100000) {
      newBet = 100000;
    }
    userDetails.userBettingAmount = newBet;
    userDetails.userWinningPrice = parseFloat(
      newBet * parseFloat(this.state.detailsOfBet.currentBettingRate)
    );
    this.setState({ userDetails });
  };
  addValue = (value) => {
    const userDetails = { ...this.state.userDetails };
    userDetails.userBettingAmount = parseInt(value);
    userDetails.userWinningPrice = Math.floor(
      parseInt(value) * parseFloat(this.state.detailsOfBet.currentBettingRate)
    );
    this.setState({ userDetails });
  };
  render() {
    const { userBettingAmount, userWinningPrice } = this.state.userDetails;
    const { currentBettingRate, question, betFor } = this.state.detailsOfBet;
    const { id: idx, option } = this.props;

    return (
      <>
        {" "}
        {this.state.isDisable && <Loader2 />}
        <div
          className="modal modal-sport fade"
          id={(
            "sportModal" +
            idx.toString().trim() +
            option.toString().trim()
          ).trim()}
          tabIndex="-1"
          aria-labelledby="sportModalTitle"
          role="dialog"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-dialog-centered" role="document">
            <div className="modal-content">
              <form onSubmit={this.doSubmit}>
                <div className="modal-header">
                  <h5 className="modal-title" id="sportModalTitle">
                    Placing a bet
                  </h5>
                  <button type="button" data-dismiss="modal" aria-label="Close">
                    <span className="icofont-ui-close btn-my"></span>
                  </button>
                </div>
                <div className="modal-body">
                  <p className="modal-sport-confrontation">{question}</p>
                  <p className="modal-sport-wager-title">
                    <span className="modal-sport-wager">{betFor}</span>{" "}
                    <span className="modal-sport-wager-count">
                      {currentBettingRate}
                    </span>
                  </p>

                  <p className="modal-sport-live">
                    <span>
                      Bet Now <LiveDot />
                    </span>
                  </p>
                  <div className="stepper-sport">
                    <div className="stepper ">
                      <input
                        
                        type="number"
                        min="100"
                        max="100000"
                        value={userBettingAmount.toString().replace(/^0+/,'')}
                        onChange={this.handleChange}
                        style={{ maxWidth: "200px", minWidth: "150px" }}
                        placeholder="$"
                      />
                    </div>
                    <div
                      className="liveBox2 container mb-2"
                      style={{ backgroud: "white" }}
                    >
                      {this.state.someOption.map(({ id, value }) => {
                        return (
                          <span
                            className="badge badge-light btn mr-2"
                            key={id}
                            value={id}
                            style={{
                              background: "green",
                              fontSize: "12px",
                              marginRight: "5px",
                              marginTop: "10px",
                            }}
                            onClick={() => this.addValue(value)}
                          >
                            {value}
                          </span>
                        );
                      })}
                    </div>
                  </div>
                </div>
                <div className="modal-footer">
                  <p className="modal-sport-stake">
                    <span className="modal-sport-stake-left">Stake</span>
                    <span className="modal-sport-stake-right">
                      {userBettingAmount}
                    </span>
                  </p>
                  <p className="modal-sport-win">
                    <span className="modal-sport-win-left">Winnings</span>
                    <span className="modal-sport-win-right">
                      {userWinningPrice.toFixed(2)}
                    </span>
                  </p>
                  <button
                    className={
                      this.state.isDisable
                        ? "modal-sport-place button button-primary button-block disabledEverything specialBtn"
                        : "modal-sport-place button button-primary button-block"
                    }
                  >
                    place bet
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default BitPopUP;
