import React from "react";
import { Input } from "./inputHelp";
import { apiEndPoint } from "../endPoins";
import http from "../services/httpServices";
import TransactionForm from "./transaction";
import { toast } from "react-toastify";
import Joi from "joi-browser";
import { getJwt } from "../services/authServices";
http.setJwt(getJwt());
class Deposite extends TransactionForm {
  state = {
    data: {
      method: "",
      amount: 0,
      account: "",
      transaction_id: "",
      superuser_account: "",
    },
    errors: {
      method: null,
      amount: null,
      transaction_id: null,
      account: null,
      superuser_account: null,
    },

    methodOption: [
      {
        id: "",
        to_show: "",
      },
    ],
    numbers: [
      {
        id: "",
        number: "",
      },
    ],
  };
  schema = {
    method: Joi.string().required().label("Method"),
    amount: Joi.number().min(100).required().label("Amount"),
    transaction_id: Joi.string().min(8).required().label("Transaction Id"),
    superuser_account: Joi.string().min(11).max(12).required().label("From "),
    account: Joi.string().required().label("Account"),
  };
  async componentDidMount() {
    try {
      const res = await http.get(
        apiEndPoint("api/transactions/available_methods/")
      );

      this.setState({ methodOption: res.data.methods });
      const target = res.data.methods[0]["id"];
      this.handleClickOfoption(target);
    } catch (e) {
      toast.error("Method Option Error");
    }
  }
  handleClickOfoption = ({ value }) => {
    const res = this.getNumbersFromId(value, this.state.methodOption);
    if (res) {
      this.setState({ numbers: res });
    }
  };
  getNumbersFromId = (id, data) => {
    for (let d of data) {
      if (d.id === id) return d.numbers;
    }
    return null;
  };
  doSubmit = async (e) => {
    try {
       await http.post(apiEndPoint("api/deposit/"), this.state.data);
      toast.success("Your request has been success");
      window.location = "/profile";
    } catch (err) {
      if (err.response && err.response.status === 400) {
        const errors = { ...this.state.errors };
        try {
          errors.amount = err.response.data.amount[0];
        } catch (err) {}

        try {
          errors.amount = err.response.data.non_field_errors[0];
          toast.error(err.response.data.non_field_errors[0]);
        } catch (err) {}

        this.setState({ errors });
      }
    }
  };

  render() {
    const { account, amount, transaction_id } = this.state.data;

    const { methodOption, numbers, errors } = this.state;
    return (
      <div className="modal fade" tabIndex="-1" role="dialog" id="Deposit">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header" style={{ background: "green" }}>
              <h5 className="modal-title">Deposit</h5>
              <button
                type="button"
                className="close"
                data-dismiss="modal"
                aria-label="Close"
              >
                <span className="icofont-ui-close btn-my"></span>{" "}
              </button>
            </div>
            <div className="modal-body">
              <form
                className="rd-form rd-mailform"
                onSubmit={this.handleSubmit}
              >
                <div className="form-input form-group mb-2">
                  <label htmlFor="exampleFormControlSelect1">
                    Select Method
                  </label>
                  <select
                    className="form-control"
                    name="method"
                    onChange={this.handleChange}
                  >
                    <option key="n/a" value="N/A">
                      Select Method
                    </option>
                    {methodOption.map(({ id }) => {
                      return (
                        <option value={id} key={id}>
                          {id}
                        </option>
                      );
                    })}
                  </select>
                  <small id="emailHelp" className="form-text text-warning">
                    {errors.method}
                  </small>
                </div>
                <div className="form-input form-group mb-2">
                  <label htmlFor="exampleFormControlSelect1">
                    Select Number
                  </label>
                  <select
                    className="form-control"
                    name="superuser_account"
                    onChange={this.handleChange}
                  >
                    <option key="n/a" value="N/A">
                      Select Number
                    </option>
                    {numbers.map(({ id, number }) => {
                      return (
                        <option value={number} key={id}>
                          {number}
                        </option>
                      );
                    })}
                  </select>
                  <small id="emailHelp" className="form-text text-warning">
                    {errors.superuser_account}
                  </small>
                </div>
                <Input
                  placeholder="Enter Amount"
                  name="amount"
                  value={amount}
                  onChange={this.handleChange}
                  type="number"
                  errors={errors.amount}
                />
                <Input
                  placeholder="From"
                  name="account"
                  value={account}
                  type="number"
                  onChange={this.handleChange}
                  errors={errors.account}
                />
                <Input
                  placeholder="transaction Id"
                  name="transaction_id"
                  value={transaction_id}
                  onChange={this.handleChange}
                  errors={errors.transaction_id}
                />
                <button
                  className="button button-lg button-primary button-block"
                  type="submit"
                >
                  Deposit
                </button>
              </form>{" "}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Deposite;
