import React, { Component } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import axios from "axios";
import { apiEndPoint } from "../endPoins";
import "./style.css";

import Loader from "../loader";

import GameHelper from "./gameHepler";
import CreateMatch from "./createMatch";
import UpdateMatch from "./updateMatch copy";
import { toast } from "react-toastify";
import CreateMatchQuestion from "./createMatchQuestion";
import { getJwt, getUserByJwt } from "../services/authServices";
import Error404 from "../404";
import Loader2 from "../loader copy";
import http from "../services/httpServices";
http.setJwt(getJwt());

class LiveGameEditor extends Component {
  state = {
    currentGameId: "",
    data: [
      {
        id: "0",
        game_name: "",
        title: "",
        start_time: "",
        end_time: "",
      },
    ],
    game_editor: false,
    allData: [
      { id: 5, name: "All Games" },
      { id: 1, name: "Football" },
      { id: 2, name: "Cricket" },
      { id: 3, name: "Tennis" },
      { id: 4, name: "Others" },
    ],
    nameOfMatchId: [],
    currentGameName: {
      gameName: "All Games",
      itemCount: "",
    },
    announcement: [],
    currentGameData: [],
    liveGameId: [],
    upcommingGameId: [],
    liveGameData: [],
    upcommingGameData: [],
    matchesQuestion: {},
    matchId: "",
    isLoading: true,
    isLive: true,
  };
  async componentDidMount() {
    try {
      const data = await axios.get(apiEndPoint("api/match/?active_only=true"));
      if (data.data.results.length > 0) {
        const currentGameName = { ...this.state.currentGameName };
        const matchesQuestion = {};
        const liveGameId = [];
        const upcommingGameId = [];
        const upcommingGameData = [];
        const liveGameData = [];
        const res = await axios.get(apiEndPoint("api/bet_scope/"));
        for (var item of res.data.results) {
          if (item.match in matchesQuestion) {
            matchesQuestion[item.match].push(item);
          } else {
            matchesQuestion[item.match] = [];
            matchesQuestion[item.match].push(item);
          }
        }
        for (var item1 of data.data.results) {
          if (item1.is_live === true) {
            liveGameId.push(item1.id);
          } else {
            upcommingGameId.push(item1.id);
          }
        }
        for (var item2 of data.data.results) {
          if (liveGameId.includes(item2.id)) {
            liveGameData.push(item2);
          } else {
            upcommingGameData.push(item2);
          }
        }

        this.setState({
          matchesQuestion,
          data: data.data.results,
          currentGameData: liveGameData,
          currentGameName,
          upcommingGameId,
          liveGameId,
          liveGameData,
          upcommingGameData,
          isLive: true,
        });

        this.setState({
          isLoading: false,
        });
      }
    } catch (err) {}
    try {
      const { game_editor } = getUserByJwt();
      this.setState({ game_editor });
    } catch (err) {}
    this.setState({
      isLoading: false,
    });
  }
  onClickHide = async ({ id, hide }) => {
    try {
      await http.patch(apiEndPoint("api/match/" + id + "/"), {
        hide: !hide,
      });
      window.location = "/livegameeditor";
    } catch (err) {
      toast.error("Error Ocur");
    }
  };
  onClickLock = async ({ id, locked }) => {
    try {
      await http.patch(apiEndPoint("api/match/" + id + "/"), {
        locked: !locked,
      });
      window.location = "/livegameeditor";
    } catch (err) {
      toast.error("Error Ocur");
    }
  };
  handleFilter = (name) => {
    const newData = [...this.state.data];
    const liveGameId = [...this.state.liveGameId];
    const upcommingGameId = [...this.state.upcommingGameId];
    const liveGameItems = [];
    const upcommingGameItems = [];
    for (var item of newData) {
      if (item.game_name.toLowerCase() === name.toLowerCase()) {
        if (liveGameId.includes(item.id)) {
          liveGameItems.push(item);
        } else if (upcommingGameId.includes(item.id)) {
          upcommingGameItems.push(item);
        }
      }
      if (name.toLowerCase() === "All Games".toLowerCase()) {
        if (liveGameId.includes(item.id)) {
          liveGameItems.push(item);
        } else if (upcommingGameId.includes(item.id)) {
          upcommingGameItems.push(item);
        }
      }
    }
    const currentGameName = { ...this.state.currentGameName };

    currentGameName.itemCount = liveGameItems.length;
    currentGameName.gameName = name;
    this.setState({
      currentGameData: liveGameItems,
      currentGameName,
      liveGameData: liveGameItems,
      upcommingGameData: upcommingGameItems,
      isLive: true,
    });
  };

  filteredForLive = (name) => {
    if (name.toLowerCase() === "upcomming") {
      this.setState({
        isLive: false,
        currentGameData: this.state.upcommingGameData || {},
      });
    } else {
      this.setState({
        isLive: true,
        currentGameData: this.state.liveGameData || {},
      });
    }
  };

  render() {
    const { isLoading, isLive, game_editor } = this.state;

    return (
      <>
        {isLoading && <Loader2 />}
        {game_editor && (
          <>
            <div
              className="liveBox2 container mb-2"
              style={{ backgroud: "white" }}
            >
              <span
                className="LiveBtn2"
                data-toggle="modal"
                data-target="#CreateMatch"
              >
                Add New Match
              </span>
            </div>
            {isLoading && <Loader />}
            {!isLoading && (
              <section className="section section-sm ">
                <div className="container">
                  <div className="gamesSliderBox">
                    <Swiper
                      breakpoints={{
                        320: {
                          slidesPerView: 3,
                          spaceBetween: 0,
                        },

                        480: {
                          slidesPerView: 4,
                          spaceBetween: 0,
                        },

                        640: {
                          slidesPerView: 5,
                          spaceBetween: 0,
                        },
                      }}
                    >
                      {this.state.allData.map(({ name, id }, index) => {
                        return (
                          <SwiperSlide
                            key={id.toString() + index.toString()}
                            className="sprite  icon"
                          >
                            <span
                              className={
                                this.state.currentGameName.gameName.toLowerCase() ===
                                name.toLocaleLowerCase()
                                  ? "sprite active icon"
                                  : " sprite  icon"
                              }
                              onClick={() =>
                                this.handleFilter(name.toLowerCase())
                              }
                            >
                              {name !== "Cricket" && (
                                <span
                                  className={
                                    "sprite icofont-" + name.toLowerCase()
                                  }
                                />
                              )}
                              {(name === "Others" || name === "All Games") && (
                                <span className="sprite icofont-bird-wings " />
                              )}
                              {name === "Cricket" && (
                                <span className="sprite icofont-helmet" />
                              )}
                              <span className="isotope-filters-list-text">
                                {name}
                              </span>
                            </span>
                          </SwiperSlide>
                        );
                      })}
                    </Swiper>
                  </div>
                  <div className="">
                    <div className="liveBox2">
                      <span
                        className={
                          !isLive
                            ? "LiveBtn2 container p-0 m-0"
                            : "LiveBtn2 container p-0 m-0 actived"
                        }
                        onClick={() => this.filteredForLive("live")}
                      >
                        Live Match
                      </span>
                      <span
                        className={
                          isLive
                            ? "LiveBtn2 container p-0 m-0"
                            : "LiveBtn2 container p-0 m-0 actived"
                        }
                        onClick={() => this.filteredForLive("upcomming")}
                      >
                        Upcoming Match
                      </span>
                    </div>
                  </div>

                  <div className="accordion " id="accordion">
                    {this.state.currentGameData &&
                      this.state.currentGameData.map(
                        (
                          {
                            game_name,
                            title,
                            start_time,
                            end_time,
                            id,
                            locked,
                            hide,
                          },
                          index
                        ) => {
                          return (
                            <span key={index.toString() + "*" + id.toString()}>
                              <UpdateMatch
                                id={id}
                                game_name={game_name}
                                start_time={start_time}
                                end_time={end_time}
                                title={title}
                                locked={locked}
                                key={id.toString() + game_name + "2"}
                              />
                              <CreateMatchQuestion
                                match={id}
                                game_name={game_name}
                                key={id.toString() + game_name + "1"}
                              />
                              {start_time && (
                                <div
                                  className="accordion"
                                  key={id.toString() + game_name}
                                >
                                  <div
                                    className="sport-table-header"
                                    id="headingOne"
                                  >
                                    <span className="mb-0">
                                      <span
                                        data-toggle="collapse"
                                        data-target={
                                          "#collapseOne" + id + game_name
                                        }
                                        aria-expanded="true"
                                        aria-controls="collapseOne"
                                      >
                                        <div className="row">
                                          <div className="col-xl-1 col-lg-1 col-md-1 col-sm-2 col-xs-2">
                                            <div className="sport-table-icon">
                                              <span
                                                className={
                                                  "sprite icofont-bird-wings"
                                                }
                                              />
                                            </div>
                                          </div>
                                          <div className="col-xl-11 col-lg-11 col-md-11 col-sm-10 col-xs-10">
                                            <span
                                              style={{
                                                color: "",
                                                fontWeight: "bold",
                                                display: "inline-block",
                                              }}
                                            >
                                              {game_name} : {title}
                                              <br />
                                            </span>{" "}
                                            {start_time.split("T")[0]}{" "}
                                            {
                                              start_time
                                                .split("T")[1]
                                                .split("+")[0]
                                            }
                                            <div
                                              className="liveBox2 "
                                              style={{ backgroud: "white" }}
                                            >
                                              <span
                                                className="LiveBtn2"
                                                data-toggle="modal"
                                                data-target={
                                                  "#UpdateMatch" +
                                                  id
                                                    .toString()
                                                    .toLowerCase()
                                                    .trim()
                                                }
                                              >
                                                Update
                                              </span>
                                              <span
                                                className="LiveBtn2"
                                                data-toggle="modal"
                                                data-target={(
                                                  "#CreateMatchQuestion" +
                                                  id.toString() +
                                                  game_name.trim()
                                                ).trim()}
                                              >
                                                Add QN
                                              </span>
                                              {hide && (
                                                <span
                                                  onClick={() =>
                                                    this.onClickHide({
                                                      id,
                                                      hide,
                                                    })
                                                  }
                                                  className="LiveBtn2"
                                                >
                                                  unhide
                                                </span>
                                              )}
                                              {!hide && (
                                                <span
                                                  onClick={() =>
                                                    this.onClickHide({
                                                      id,
                                                      hide,
                                                    })
                                                  }
                                                  className="LiveBtn2"
                                                >
                                                  Hide
                                                </span>
                                              )}
                                              {locked && (
                                                <span
                                                  onClick={() =>
                                                    this.onClickLock({
                                                      id,
                                                      locked,
                                                    })
                                                  }
                                                  className="LiveBtn2"
                                                >
                                                  unLock
                                                </span>
                                              )}{" "}
                                              {!locked && (
                                                <span
                                                  onClick={() =>
                                                    this.onClickLock({
                                                      id,
                                                      locked,
                                                    })
                                                  }
                                                  className="LiveBtn2"
                                                >
                                                  Lock
                                                </span>
                                              )}
                                            </div>
                                          </div>
                                        </div>
                                      </span>
                                    </span>
                                  </div>
                                  <div
                                    id={"collapseOne" + id + game_name}
                                    className="collapse show"
                                    aria-labelledby="headingOne"
                                    data-parent="#accordion"
                                  >
                                    <div className="panel-body">
                                      <div
                                        className="accordion"
                                        id="accordion2"
                                      >
                                        {this.state.matchesQuestion[id] &&
                                          this.state.matchesQuestion[id].map(
                                            (
                                              { question, ...restProps },
                                              index
                                            ) => {
                                              return (
                                                <GameHelper
                                                  key={
                                                    id.toString() +
                                                    "*" +
                                                    question +
                                                    "*" +
                                                    index.toString()
                                                  }
                                                  {...restProps}
                                                  question={question}
                                                  is_live={this.state.isLive}
                                                />
                                              );
                                            }
                                          )}
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              )}
                              {!start_time && (
                                <div>
                                  <h1>No data found</h1>
                                </div>
                              )}
                            </span>
                          );
                        }
                      )}
                  </div>
                </div>
              </section>
            )}
            <CreateMatch />
          </>
        )}
        {!game_editor && !isLoading && <Error404 />}
      </>
    );
  }
}

export default LiveGameEditor;
