import React, { Component } from "react";
import { getCurrentClub } from "../services/authServices";
import Loader2 from "../loader copy";
import Error404 from "../404";
import Notification from "../notification/notification";

class ClubProfile extends Component {
  state = {
    user: [],
    isLoading: true,
  };
  async componentDidMount() {
    try {
      const { data } = await getCurrentClub();
      
      this.setState({ user: data || {} });
    } catch (e) {}
    this.setState({ isLoading: false });
  }

  render() {
    const { balance, name } = this.state.user;
    const { isLoading } = this.state;
    return (
      <>
        {isLoading && <Loader2 />}
        {!isLoading && name && (
          <div className="card user-card-full">
            <div className=" m-l-0 m-r-0">
              <div className=" bg-c-lite-green user-profile">
                <div className="card-block text-center text-white">
                  <div className="m-b-25">
                    {" "}
                    <img
                      src="https://img.icons8.com/bubbles/100/000000/user.png"
                      className="img-radius"
                      alt="UserProfile"
                    />{" "}
                  </div>
                  <h6 className="f-w-600">
                    Name : <span className="">{name}</span>
                  </h6>
                  <p>
                    Current Blance :{" "}
                    <span className="icofont-taka"> {balance.toFixed(4)} </span>{" "}
                  </p>
                  <p
                    className="icofont-notification"
                    data-toggle="modal"
                    data-target="#Notifications"
                  ></p>
                  <i className=" mdi mdi-square-edit-outline feather icon-edit m-t-10 f-16"></i>
                </div>
              </div>
            </div>{" "}
            <button
              data-toggle="modal"
              data-target="#Transfer"
              className="button button-lg button-primary button-block "
            >
              Transfer
            </button>
            <Notification />
          </div>
        )}
        {!isLoading && !name && <Error404 />}
      </>
    );
  }
}

export default ClubProfile;
