import React, { Component } from "react";

import { apiEndPoint } from "../endPoins";
import axios from "axios";
import { getJwt } from "../services/userService";
import { toast } from "react-toastify";
import Loader2 from "../loader copy";
import { Input } from "../login/inputHelp";
import Joi from "joi-browser";
axios.defaults.headers.common["x-auth-token"] = getJwt();

class BetUpdate extends Component {
  state = {
    data: {
      question: "",
      option_1: "",
      option_1_rate: "",
      option_2: "",
      option_2_rate: "",
      option_3: "",
      option_3_rate: "",
      option_4: "",
      option_4_rate: "",
      locked: "",
      id: "",
      is_live: "",
      hide: "",
      winner: "",
    },
    lock: [
      { id: 1, value: true, name: "true" },
      { id: 2, value: false, name: "false" },
    ],



    errors: [],
    isDisable: false,
  };
  schema = {
    question: Joi.string().required().label("Question"),
    option_1: Joi.string().max(1000).required().label("Option 1"),
    option_1_rate: Joi.number().min(1).required().label("Optin 1 Rate"),
    option_2: Joi.string().min(1).required().label("Option 2"),
    option_2_rate: Joi.number().min(1).required().label("Option 2 Rate"),
    option_3_rate: Joi.number().min(1).label("Option 3 rate"),
    option_4_rate: Joi.number().min(1).label("Option 4 rate"),
    option_4: Joi.string().allow("").allow(null).label("Option 4"),
    option_3: Joi.string().allow("").allow(null).label("Option 3"),
    is_live: Joi.bool().allow(""),
    is_locked: Joi.bool().allow(""),
    id: Joi.number(),
    hide: Joi.bool(),
  };
  componentDidMount() {
    const data = { ...this.props };

    this.setState({ data });
  }
  validate = () => {
    const options = { abortEarly: false };
    const { error } = Joi.validate(this.state.data, this.schema, options);

    if (!error) return null;

    const errors = {};
    for (let item of error.details) errors[item.path[0]] = item.message;
    return errors;
  };

  doSubmit = async (e) => {
    e.preventDefault();
    const errors = this.validate();

    this.setState({ errors: errors || {} });
    if (errors) return;
    this.setState({ isDisable: true });
    try {
      const data = { ...this.state.data };
      data.is_locked = data.is_locked === "true";
      data.hide = data.hide === "true";
      data.locked = data.is_locked;
      const { id } = this.state.data;
      await axios.patch(apiEndPoint("api/bet_scope/" + id + "/"), data);
      toast.success("Question Updated !");
      window.location = "./livegameeditor";
    } catch (err) {
      try {
        try {
          toast.error(err.response.data.bet_scope[0]);
        } catch (err) {}
        try {
          toast.error(err.response.data.non_field_errors[0]);
        } catch (err) {}
      } catch (err) {
        toast.error("Sorry please cheack !");
      }
    }
    this.setState({ isDisable: false });
  };

  handleChange = ({ currentTarget: input }) => {
    const data = { ...this.state.data };
    data[input.name] = input.value;
    this.setState({ data });
  };

  render() {
    const {
      question,
      option_1,
      option_1_rate,
      option_2,
      option_2_rate,
      option_3,
      option_3_rate,
      option_4,
      option_4_rate,
      id,
    } = this.state.data;
    const { errors } = this.state;
    return (
      <>
        {" "}
        {this.isDisable && <Loader2 />}
        {id && (
          <div
            className="modal fade"
            id={"sportModal" + id.toString().toLowerCase().trim()}
            tabIndex="-1"
            role="dialog"
          >
            <div className="modal-dialog" role="document">
              <div className="modal-content">
                <div className="modal-header" style={{ background: "green" }}>
                  <h5 className="modal-title">Update Question</h5>
                  <button type="button" data-dismiss="modal" aria-label="Close">
                    <span className="icofont-ui-close btn-my"></span>
                  </button>
                </div>
                <div className="justify-content-center modal-body">
                  <form className="form-group" onSubmit={this.doSubmit}>
                    <Input
                      placeholder="Enter question"
                      name="question"
                      value={question}
                      onChange={this.handleChange}
                      errors={errors.question}
                    />

                    <Input
                      name="option_1"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_1}
                      placeholder="Enter Option 1"
                      errors={errors.option_1}
                    />
                    <Input
                      name="option_1_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_1_rate}
                      placeholder="Enter Option 1 rate"
                      type="number"
                      step="any"
                      errors={errors.option_1_rate}
                    />
                    <Input
                      name="option_2"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_2}
                      placeholder="Enter Option 2"
                      errors={errors.option_2}
                    />
                    <Input
                      name="option_2_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_2_rate}
                      placeholder="Enter Option 2 Rate"
                      type="number"
                      step="any"
                      errors={errors.option_2_rate}
                    />
                    <Input
                      name="option_3"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_3}
                      placeholder="Enter Option 3"
                      errors={errors.option_3}
                    />
                    <Input
                      name="option_3_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_3_rate}
                      placeholder="Enter Option 3 Rate"
                      type="number"
                      step="any"
                      errors={errors.option_3_rate}
                    />
                    <Input
                      name="option_4"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_4}
                      placeholder="Enter Option 4"
                      errors={errors.option_4}
                    />
                    <Input
                      name="option_4_rate"
                      onChange={this.handleChange}
                      className="form-control"
                      value={option_4_rate}
                      placeholder="Enter Option 4 Rate"
                      type="number"
                      step="any"
                      errors={errors.option_4_rate}
                    />

                    <button
                      className="button button-lg button-primary button-block"
                      type="submit"
                    >
                      Update Question
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        )}
        <></>
      </>
    );
  }
}

export default BetUpdate;
