import React, { Component } from "react";
import { Input } from "../login/inputHelp";
import moment from "moment";
import { apiEndPoint } from "../endPoins";
import axios from "axios";
import { toast } from "react-toastify";
import Joi from "joi-browser";
class CreateMatch extends Component {
  state = {
    matchData: {
      end_time: "",
      game_name: "",
      start_time: "",
      title: "",
    },
    errors: {
      end_time: null,
      start_time: null,
      title: null,
      game_name: null,
    },
    matchType: [
      { id: 1, name: "football" },
      { id: 2, name: "cricket" },
      { id: 3, name: "tennis" },
      { id: 4, name: "others" },
    ],
  };
  schema = {
    end_time: Joi.string()

      .min(6)
      .max(1466)
      .required()
      .label("End Time"),
    start_time: Joi.string().min(3).max(866).required().label("Star Time"),
    game_name: Joi.string().required().label("Game Name"),
    title: Joi.string().min(6).required().label("Title"),
  };
  handleChange = ({ currentTarget: input }) => {
    const matchData = { ...this.state.matchData };

    matchData[input.name] = input.value;

    this.setState({ matchData });
  };
  validate = () => {
    const options = { abortEarly: false };
    const { error } = Joi.validate(this.state.matchData, this.schema, options);

    if (!error) return null;

    const errors = {};
    for (let item of error.details) errors[item.path[0]] = item.message;
    return errors;
  };
  handleSubmit = async (e) => {
    e.preventDefault();
    const errors = this.validate();
    this.setState({ errors: errors || {} });
    if (errors) return;
    const matchData = { ...this.state.matchData };
    const time = moment(matchData.start_time).format();
    matchData.start_time = time;
    const time2 = moment(matchData.end_time).format();
    matchData.end_time = time2;
    try {
      await axios.post(apiEndPoint("api/match/"), matchData);
      toast.success("A Match Created");
      window.location.reload();
    } catch (err) {
      toast.error("Can not create a match");
    }
  };
  render() {
    const { title, start_time, end_time, game_name } = this.state.matchData;
    const { errors } = this.state;
    return (
      <>
        <div
          className="modal fade"
          id="CreateMatch"
          tabIndex="-1"
          role="dialog"
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header" style={{ background: "green" }}>
                <h5 className="modal-title">Create Match</h5>
                <button type="button" data-dismiss="modal" aria-label="Close">
                  <span className="icofont-ui-close btn-my"></span>
                </button>
              </div>
              <div className="modal-body">
                <form
                  className="rd-form rd-mailform"
                  onSubmit={this.handleSubmit}
                >
                  <Input
                    placeholder="Enter Game Title"
                    name="title"
                    value={title}
                    onChange={this.handleChange}
                    errors={errors.title}
                  />
                  <Input
                    name="start_time"
                    onChange={this.handleChange}
                    className="form-control"
                    value={start_time}
                    placeholder="Enter Start Time"
                    type="datetime-local"
                    errors={errors.start_time}
                  />
                  <Input
                    name="end_time"
                    onChange={this.handleChange}
                    className="form-control"
                    value={end_time}
                    placeholder="Enter End Time"
                    type="datetime-local"
                    errors={errors.end_time}
                  />
                  <div className="form-input form-group mb-4">
                    <label htmlFor="exampleFormControlSelect1">
                      Select Match Type
                    </label>

                    <select
                      name="game_name"
                      onChange={this.handleChange}
                      className="form-control"
                      value={game_name}
                    >
                      <option key="n/a">Select Match Type</option>
                      {this.state.matchType.map(({ id, name }) => {
                        return (
                          <option value={name} key={id}>
                            {name.toUpperCase()}
                          </option>
                        );
                      })}
                    </select>
                    <small className="text-danger">{errors.game_name}</small>
                  </div>
                  <button
                    className="button button-lg button-primary button-block"
                    type="submit"
                  >
                    Create Match
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default CreateMatch;
