import React, { Component } from "react";
import http from "../services/httpServices";
import { getJwt } from "../services/userService";
import { apiEndPoint } from "../endPoins";
import { toast } from "react-toastify";
http.setJwt(getJwt());
http.setJwtForClub(getJwt());
class Winner extends Component {
  state = {
    win: [
      { id: 1, value: "option_1", name: "" },
      { id: 2, value: "option_2", name: "" },
      { id: 3, value: "option_3", name: "" },
      { id: 4, value: "option_4", name: "" },
    ],
    selected: "",
    betId: "",
  };

  onClick = (e) => {
    this.setState({ selected: e });
  };
  doSubmit = async (e) => {
    e.preventDefault();
    try {
      await http.patch(apiEndPoint("api/bet_scope/" + this.state.betId + "/"), {
        winner: this.state.selected.value,
      });
      toast.success("Winner Given !");
      window.location = "/livegameeditor";
    } catch (err) {
      console.log(err.response);
      toast.error("Somthing went wrong");
    }
  };
  componentDidMount() {
    try {
      const { option_1, option_2, option_3, option_4, betId } = {
        ...this.props,
      };
      const win = [...this.state.win];

      win[0].name = option_1 || "";
      win[1].name = option_2 || "";
      win[2].name = option_3 || "";
      win[3].name = option_4 || "";
      this.setState({ win, betId });
    } catch (err) {}
  }

  render() {
    const { win } = this.state;
    const { selected } = this.state;
    const { betId } = this.state;
    return (
      <>
        <li className="liveBox2 LiveBtn2 dropdown">
          <span
            className="liveBox2 LiveBtn2 mr-2 ml-3 p-1 dropdown-toggle"
            id="navbarDarkDropdownMenuLink"
            role="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            Select Winner
          </span>
          <ul
            className="dropdown-menu dropdown-menu-dark"
            aria-labelledby="navbarDarkDropdownMenuLink"
          >
            {win.map((item, index) => {
              if (!item.name) return null;
              return (
                <li
                  key={index}
                  onClick={() => this.onClick(item)}
                  data-toggle="modal"
                  data-target={"#exampleModal" + betId.toString()}
                >
                  <span
                    className="dropdown-item"
                    key={index}
                    style={{ color: "red", fontSize: "14px" }}
                  >
                    {item.name}
                  </span>
                </li>
              );
            })}
          </ul>
        </li>
        <div
          className="modal fade"
          id={"exampleModal" + betId.toString()}
          tabIndex="-1"
          role="dialog"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">
                  Winner
                </h5>
              </div>
              <div className="modal-body">
                <p style={{ fontSize: "20px", color: "red" }}>
                  Are you sure ?{" "}
                </p>{" "}
                <br />
                <p className="h4">
                  {" "}
                  Winner is <span className="green">{selected.name}</span>
                </p>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-secondary"
                  data-dismiss="modal"
                >
                  No
                </button>
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={this.doSubmit}
                >
                  I am sure
                </button>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Winner;
