import React, { Component } from "react";
import { apiEndPoint } from "../endPoins";
import { getUserByJwt } from "../services/authServices";
import { Input } from "../login/inputHelp";
import { getJwt, login } from "../services/userService";
import Loader2 from "../loader copy";
import Notification from "../notification/notification";
import http from "../services/httpServices";
import { toast } from "react-toastify";
http.setJwtForClub(getJwt());
http.setJwt(getJwt());

class OriginalProfile extends Component {
  state = {
    userDetails: {
      id: 1,
      referred_by: {
        username: "",
        first_name: "",
        last_name: "",
      },
      is_club_admin: true,
      refer_set: [],
      last_login: "",
      is_superuser: true,
      username: "",
      first_name: "",
      last_name: "",
      is_staff: true,
      is_active: true,
      date_joined: "",
      balance: "",
      email: "",
      phone: "",
      game_editor: true,
      user_club: {
        id: 1,
        name: "",
        balance: 0.0,
        admin: 1,
      },
    },
    updateUserDetails: {
      phone: "",
      first_name: "",
      user_club: "",
      password: "",
      oldPassword: "",
    },
    allClube: [{ name: "", id: "" }],
    isLoading: true,
  };
  async componentDidMount() {
    try {
      const userDetails = getUserByJwt();
      this.setState({ userDetails });
    } catch (err) {}
    const promise = await http.get(apiEndPoint("api/club/"));
    const dataOfUser = await http.get(apiEndPoint("api/user-detail-update/"));

    this.setState({
      allClube: promise.data.results,
    });
    const data1 = {
      phone: "",
      first_name: "",
      user_club: "",
      password: "",
      oldPassword: "",
    };
    if (dataOfUser.data) {
      data1.phone = dataOfUser.data.phone;
      data1.first_name = dataOfUser.data.first_name;
      data1.user_club = dataOfUser.data.user_club.id;
      this.setState({
        userDetails: dataOfUser.data || {},
        updateUserDetails: data1,
      });
    }
    this.setState({
      isLoading: false,
    });
  }
  handleChange = ({ currentTarget: input }) => {
    const updateUserDetails = { ...this.state.updateUserDetails };
    updateUserDetails[input.name] = input.value;
    this.setState({ updateUserDetails });
  };

  onSubmit = async (e) => {
    e.preventDefault();
    try {
      await http.patch(
        apiEndPoint("api/user-detail-update/"),
        this.state.updateUserDetails
      );
      window.location = "/profile";
    } catch (err) {
      console.log(err.response);
    }
  };

  onSubmit2 = async (e) => {
    e.preventDefault();
    try {
      const user1 = {
        username: this.state.userDetails.username,
        password: this.state.updateUserDetails.oldPassword,
      };
      try {
        const { data } = await login(user1);
        localStorage.setItem("token", data.jwt);
      } catch (err) {
        toast.error("Old password does not match !");
        return;
      }

      await http.post(apiEndPoint("api/change-password/"), {
        password: this.state.updateUserDetails.password,
      });

      const user = {
        username: this.state.userDetails.username,
        password: this.state.updateUserDetails.password,
      };
      if (user.password.length < 6) {
        toast.error("Password length must be at least 6 word");
        return;
      }
      const { data } = await login(user);
      localStorage.setItem("token", data.jwt);
      toast.success("Password update Successfull !");
      window.location = "/profile";
    } catch (err) {}
  };
  render() {
    if (this.state.userDetails.admin) {
      return null;
    } else {
      const {
        username,
        balance,
        email,
        phone,
        first_name,
        referred_by,
        date_joined,
        earn_from_refer,
        club_detail,
      } = this.state.userDetails;

      const { updateUserDetails } = this.state;
      const { isLoading } = this.state;

      return (
        <>
          {isLoading && <Loader2 />}
          {!isLoading && (
            <>
              <div className="card user-card-full">
                <div className="row m-l-0 m-r-0">
                  <div className="col-sm-4 bg-c-lite-green user-profile">
                    <div className="card-block text-center text-white">
                      <div className="m-b-25">
                        {" "}
                        <img
                          src="https://img.icons8.com/bubbles/100/000000/user.png"
                          className="img-radius"
                          alt="UserProfile"
                        />{" "}
                      </div>
                      <h6 className="f-w-600">
                        Username : <span className="">{username}</span>
                      </h6>
                      <p>
                        Current Blance :{" "}
                        <span className="icofont-taka"> {balance} </span>{" "}
                      </p>
                      <p
                        className="icofont-notification"
                        data-toggle="modal"
                        data-target="#Notifications"
                      ></p>
                      <i className=" mdi mdi-square-edit-outline feather icon-edit m-t-10 f-16"></i>
                    </div>
                  </div>
                  <div className="col-sm-8">
                    <div className="card-block">
                      <h6 className="m-b-20 p-b-5 b-b-default f-w-600">
                        Information
                      </h6>
                      <div className="row">
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-user" /> Name
                          </p>
                          <h6 className="text-muted f-w-400">{first_name}</h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-phone" /> Phone
                          </p>
                          <h6 className="text-muted f-w-400">{phone}</h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-email" /> Email
                          </p>
                          <h6 className="text-muted f-w-400">{email}</h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-user" /> User Name
                          </p>
                          <h6 className="text-muted f-w-400">{username}</h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-cube" /> Refered BY
                          </p>
                          <h6 className="text-muted f-w-400 green">
                            {referred_by && referred_by.username}
                            {!referred_by.username && "Null"}
                          </h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-ui-home" /> Clube Name
                          </p>
                          <h6 className="text-muted f-w-400 green">
                            {club_detail.name && club_detail.name}
                          </h6>
                        </div>
                      </div>
                      <div className="row">
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-cube" /> Join Data
                          </p>
                          <h6 className="text-muted f-w-400 green">
                            {date_joined.split("T")[0]}
                          </h6>
                        </div>
                        <div className="col-sm-6">
                          <p className="m-b-10 f-w-600">
                            {" "}
                            <span className="icofont-cube" /> Earn from referred
                          </p>
                          <h6 className="text-muted f-w-400 green">
                            {earn_from_refer}
                          </h6>
                        </div>
                      </div>
                    </div>
                    <button
                      data-toggle="modal"
                      data-target="#ChangeInformation"
                      className="button button-lg button-primary button-block "
                    >
                      Change Information
                    </button>
                  </div>
                </div>
              </div>
              <div
                className="modal fade"
                id="ChangeInformation"
                tabIndex="-1"
                role="dialog"
              >
                <div className="modal-dialog" role="document">
                  <div className="modal-content">
                    <div
                      className="modal-header"
                      style={{ background: "green" }}
                    >
                      <h5 className="modal-title">Change Information</h5>
                      <button
                        type="button"
                        data-dismiss="modal"
                        aria-label="Close"
                      >
                        <span className="icofont-ui-close btn-my"></span>{" "}
                      </button>
                    </div>
                    <div className="modal-body">
                      <form
                        className="rd-form rd-mailform"
                        onSubmit={this.onSubmit}
                      >
                        <Input
                          placeholder="Enter new Full Name"
                          name="first_name"
                          value={updateUserDetails.first_name}
                          onChange={this.handleChange}
                        />
                        <Input
                          placeholder="Enter new phone number"
                          name="phone"
                          value={updateUserDetails.phone}
                          onChange={this.handleChange}
                        />
                        <div className="form-input form-group mb-4">
                          <label htmlFor="exampleFormControlSelect1">
                            Change Club
                          </label>
                          <select
                            name="user_club"
                            onChange={this.handleChange}
                            className="form-control"
                            value={updateUserDetails.user_club}
                          >
                            <option key="n/a">Select Club</option>
                            {this.state.allClube.map(({ id, name }) => {
                              return (
                                <option value={id} key={id}>
                                  {name}
                                </option>
                              );
                            })}
                          </select>
                        </div>

                        <button
                          className="button button-lg button-primary button-block"
                          type="submit"
                        >
                          Update
                        </button>
                      </form>{" "}
                      <form
                        className="rd-form rd-mailform"
                        onSubmit={this.onSubmit2}
                      >
                        <Input
                          placeholder="Enter old Password"
                          name="oldPassword"
                          value={updateUserDetails.oldPassword}
                          onChange={this.handleChange}
                          type="password"
                        />
                        <Input
                          placeholder="Enter new Password"
                          name="password"
                          value={updateUserDetails.password}
                          onChange={this.handleChange}
                          type="password"
                        />
                        <button
                          className="button button-lg button-primary button-block"
                          type="submit"
                        >
                          Update Password
                        </button>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </>
          )}
          <Notification />
        </>
      );
    }
  }
}

export default OriginalProfile;
